import React, {Component} from 'react';
import './style.css';
import article from '../../fixtures';
import {Link} from "react-router-dom";
import {getValues} from '../../tools';

export default class Detail extends Component {

    render() {
        const {match} = this.props;
        const getParams = getValues(article, match.params.id);
        return (
            <div className="col-xs-12">
                <div className="row">
                    {/*<div className="col-sm-6 col-xs-12">*/}
                    {/*<img className="screenshot-project" src={getParams[0].image} alt=" "/>*/}
                    {/*<a href={getParams[0].link} target="_blank">*/}
                    {/*  */}
                    {/*</a>*/}
                    {/*</div>*/}
                    <div className="col-sm-6 col-xs-12">
                        <h1>{getParams[0].title}</h1>
                        <p>{getParams[0].description}</p>
                    </div>
                </div>

                <div className="row">
                    <div className="container">
                        <div className="row text-center">
                            <div className="devices">
                                <div className="imgDevices imgTablet">
                                    <img src={getParams[0].tabletImg} alt="tablet"/>
                                </div>
                                <div className="imgDevices imgDesktop">
                                    <img src={getParams[0].deskImg} alt="desktop"/>
                                </div>
                                <div className="imgDevices imgMobile">
                                    <img src={getParams[0].phoneImg} alt="mobile"/>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="btns-links">
                        <Link to="/">
                            <button>  </button>
                        </Link>
                        <form action={getParams[0].link} target="_blank">
                            <button>  </button>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}


